.navBarContent{
    width: 80vw;
    position: absolute;
    font-family: 'Montserrat', sans-serif;
    font-size: 130px;
    font-weight: 900;
    height: 100vh;
    z-index: 200;
    margin-top: 40px;
    left: 10vw;
    animation: showNav 1s ease-in forwards;
}

@keyframes showNav{
    0%{opacity: 0;}
    50%{opacity: 0;}
    100%{opacity: 1;}
}
.navBarContent div{
    margin: 0 auto;
    width: max-content;
    height: 100vh;
}
.navBarContent a{
    display: block;
    margin: 0 auto;
    width: max-content;
    text-decoration: none;
    color: #110935;
    transition: all ease 0.3s;
}

.navBarContent a:hover{
    color: transparent;
    -webkit-text-stroke: 3px #110935;
}

